import React from 'react';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';

import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';

import tunic from '../assets/shop/tunic.png';
import rightSandal from '../assets/shop/r-sandal.png';
import leftSandal from '../assets/shop/l-sandal.png';
import helmet from '../assets/shop/helmet.png';
import shield from '../assets/shop/shield.png';
import breastplate from '../assets/shop/breastplate.png';
import rightGreave from '../assets/shop/r-greave.png';
import leftGreave from '../assets/shop/l-greave.png';
import spear from '../assets/shop/spear.png';

import { getParams } from '../utils/url-parser';

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  gridList: {
    width: 800,
    height: 800
  }
});

const DESCRIPTION_MAP = {
  ILIAD: 'Hephaestus has willingly agreed to make some new armour for you. But with his ability to make indestructible arms, he’s a pretty busy god. You’ll have to pay some high Nectar prices and submit to his limit of two items per week. Better put in your order now if you want to get back to the battle soon!',
  AENEID: 'Vulcan has willingly agreed to make some new armour for you since, well, you are Venus’ son. *Shifty eyes* But with his ability to make indestructible arms, he’s a pretty busy god. Better put in your order now if you want to avenge Pallas soon! Cough up those Nectar Points.'

}

class Shop extends React.Component {
  constructor(props) {
    super(props);
    this.items = [
      { name: 'Tunic', image: tunic, price: 400 },
      { name: 'Right Sandal', image: rightSandal, price: 400 },
      { name: 'Left Sandal', image: leftSandal, price: 400 },
      { name: 'Helmet', image: helmet, price: 1000 },
      { name: 'Breastplate', image: breastplate, price: 1000 },
      { name: 'Shield', image: shield, price: 1500 },
      { name: 'Right Greave', image: rightGreave, price: 400 },
      { name: 'Left Greave', image: leftGreave, price: 400 },
      { name: 'Spear', image: spear, price: 800 },
    ];

    const { mode } = getParams();
    this.description = DESCRIPTION_MAP[mode];
  }
  render() {
    const { classes } = this.props;
    return <div className={classes.root}>
      <Typography variant="h5" component="h2">{this.description}</Typography>
      
      <GridList cols={3} className={classes.gridList}>
        {this.items.map(tile => (
          <GridListTile key={tile.image} style={{height: '33.3%'}}>
            <img src={tile.image} alt={tile.name} />
            <GridListTileBar
              title={tile.name}
              subtitle={<span>Price: {tile.price} NP</span>}
            />
          </GridListTile>
        ))}
      </GridList>
    </div>
  }
}

export default withStyles(styles)(Shop);